import { CrossCellMessageService } from "../communcation/crossCellMessageService";
import { CodeLibraryProviderInstance } from "../storage/libraryProvider";
import { appStoreBuilder } from "./vanilla";

export * from "./vanilla";
export * from "./model";
export const appStore = appStoreBuilder({
  libraryProvider: CodeLibraryProviderInstance,
  messageService: CrossCellMessageService,
});

CrossCellMessageService.getMessage().subscribe((message) => {
  switch (message.action) {
    case "OPEN_LIBRARIES_LIST_WINDOW": {
      appStore.getState().setCrossCellState({
        kind: "librariesList",
        diff: {},
        currentBaseCodeCellId: message.activeAznbCellId,
        displayedModalWindow: "librariesList",
        attachedLibraryData: message.attachedLibraryData,
      });
      break;
    }
    case "HIDE_LIBRARY_EDIT_WINDOW": {
      const currentCrossCellStateSlice = appStore.getState().crossCellState;
      currentCrossCellStateSlice.kind === "crud" &&
        appStore.getState().setCrossCellState({
          kind: "librariesList",
          diff: currentCrossCellStateSlice.diff,
          currentBaseCodeCellId: currentCrossCellStateSlice.currentBaseCodeCellId,
          displayedModalWindow: "librariesList",
          attachedLibraryData: currentCrossCellStateSlice.attachedLibraryData,
        });
      break;
    }

    case "OPEN_LIBRARY_RENAME_WINDOW": {
      const currentCrossCellStateSlice = appStore.getState().crossCellState;
      currentCrossCellStateSlice.kind === "crud" &&
        appStore.getState().setCrossCellState({
          kind: "crud",
          displayedModalWindow: "upsertLibraryName",
          diff: currentCrossCellStateSlice.diff,
          currentBaseCodeCellId: currentCrossCellStateSlice.currentBaseCodeCellId,
          attachedLibraryData: currentCrossCellStateSlice.attachedLibraryData,
          currentLibraryItem: currentCrossCellStateSlice.currentLibraryItem,
        });
      break;
    }
    case "GET_CURRENT_LIBRARY": {
      const currentCrossCellStateSlice = appStore.getState().crossCellState;
      currentCrossCellStateSlice.kind === "crud" &&
        CrossCellMessageService.sendMessage({
          action: "SEND_CURRENT_LIBRARY",
          library: currentCrossCellStateSlice.currentLibraryItem,
          activeAznbCellId: currentCrossCellStateSlice.currentBaseCodeCellId,
        });

      break;
    }
    case "UPDATE_OUTPUT_PANEL_DATA": {
      appStore.getState().setLibrarySourceEditorOutputPanelData(message.data);
      break;
    }

    default:
      break;
  }
});
