import { IIpcRenderer, MinecraftResponse } from "../types/ipcRenderer";
import { IEvent } from "..";
import { ILogger } from "../types/ILogger";
import { ICommand, IListenerCommand } from "./Commands/Commands";

export interface IMinecraftCommander extends IEvent {
  send<Result>(command: ICommand<Result>): Promise<Result>;
  subscribe<Result>(command: IListenerCommand<Result>, callback: (result: Result) => void): void;
}

type RequestId = string;
type Resolve = (value?: any) => void;
type Reject = (value?: any) => void;

export class MinecraftCommander implements IMinecraftCommander {
  private static instance: MinecraftCommander;
  private ipcRenderer: IIpcRenderer;
  private logger: ILogger;
  private eventQueue: Map<RequestId, [ICommand<any>, Resolve, Reject]>;
  private subscriptionQueue: Map<string, [IListenerCommand<any>, (callback: any) => void]>;

  static getInstance(ipcRenderer: IIpcRenderer, logger: ILogger = console): MinecraftCommander {
    if (!MinecraftCommander.instance) {
      MinecraftCommander.instance = new MinecraftCommander(ipcRenderer, logger);
    }

    return MinecraftCommander.instance;
  }

  private constructor(ipcRenderer: IIpcRenderer, logger: ILogger = console) {
    this.ipcRenderer = ipcRenderer;
    this.logger = logger;
    this.eventQueue = new Map();
    this.subscriptionQueue = new Map();

    this.ipcRenderer.on("responseFromApp", (_eventName: string, event: string) => {
      try {
        const data: MinecraftResponse = JSON.parse(event);
        if (data && data.header && data.body) {
          if (data.header.messagePurpose === "event") {
            const subscription = this.subscriptionQueue.get(data.body.eventName || "");
            if (subscription) {
              const [command, callback] = subscription;
              try {
                const payload = command.parse(data.body);
                callback(payload);
              } catch (e) {
                logger.log(`Swallowing: ipcRenderer Response Error: ${e}`);
              }
            }
          } else {
            const event = this.eventQueue.get(data.header.requestId);
            if (event) {
              const [command, resolve, reject] = event;
              this.eventQueue.delete(data.header.requestId);
              try {
                resolve(command.responseHandler(data));
              } catch (e) {
                reject(e);
              }
            }
          }
        }
      } catch (e) {
        this.logger.error(`Swallowing: ${e.message}`);
      }
    });
  }

  public subscribe<Result>(command: IListenerCommand<Result>, callback: (result: Result) => void): void {
    if (this.ipcRenderer && command.request.body.eventName) {
      this.subscriptionQueue.set(command.request.body.eventName, [command, callback]);
      this.ipcRenderer.sendToHost("sendToApp", command.request);
    } else {
      throw new Error("Error: Could not send command. IpcRenderer not available OR Subscription command is invalid.");
    }
  }

  public send<Result>(commandJson: ICommand<Result>): Promise<Result> {
    if (this.ipcRenderer) {
      console.log(`Sending Message: ${commandJson.request}`);
      return new Promise<Result>((resolve, reject) => {
        this.eventQueue.set(commandJson.request.header.requestId, [commandJson, resolve, reject]);
        this.ipcRenderer.sendToHost("sendToApp", commandJson.request);
      });
    } else {
      throw new Error("Error: Could not send command. IpcRenderer not available.");
    }
  }

  on(event: string, listener: EventListener): void {
    this.ipcRenderer.on(event, listener);
  }

  removeListener(event: string, listener: EventListener): void {
    this.ipcRenderer.removeListener(event, listener);
  }

  once(event: string, listener: EventListener): void {
    this.ipcRenderer.once(event, listener);
  }
}
