import * as D from "io-ts/Decoder";
import { pipe } from "fp-ts/function";
import { OnDiskOutput } from "./OnDiskOutput";
import { JSONObject } from "./JSONObject";
import { LibraryId, LibraryTimestamp } from "./UserDefinedLibraries";

const CodeStatus = D.union(D.literal("error"), D.literal("ok"));
const LibraryUI = D.partial({
  expand: D.boolean,
  codeStatus: CodeStatus,
});
const MultiLineString = D.union(D.string, D.array(D.string));

const CodeCellMinecraftMetadata = D.partial({
  closeCodeBuilderOnRun: D.boolean,
  onCodeCancelledCommand: D.string,
  onCodeErrorCommand: D.string,
  onCodeStartedCommand: D.string,
  onCodeStoppedCommand: D.string,
  onCodeSuccessfulCommand: D.string,
  overridePython: D.string,
  numberOfLines: D.number,
  readonly: D.boolean,
  enableCodeLibraryButton: D.boolean,
  baseCodeCellName: D.string,
  default_code: MultiLineString,
  resetWorldCommand: D.string,
  hidden: D.boolean,
  attachedLibraries: D.array(D.record(LibraryUI)),
  // ---------------
  // Base Code Cell
  connectedLibraries: D.array(LibraryId),
  // Library Code Cell
  libraryId: LibraryId,
  timestamp: LibraryTimestamp,
  isPredefined: D.boolean,
  isPreAttached: D.boolean,
  codeStatus: CodeStatus,
  // Base and Library Code Cell
  baseKey: D.string,
  errorData: D.string,
  expand: D.boolean,
});
export type CodeCellMinecraftMetadata = D.TypeOf<typeof CodeCellMinecraftMetadata>;
const CodeCellMetadata = D.partial({
  minecraft: CodeCellMinecraftMetadata,
});

const CodeCell = D.struct({
  cell_type: D.literal("code"),
  source: MultiLineString,
  execution_count: D.nullable(D.number),
  metadata: CodeCellMetadata,
  outputs: OnDiskOutput,
});

export type CodeCell = D.TypeOf<typeof CodeCell>;
const MarkdownCell = D.struct({
  cell_type: D.literal("markdown"),
  metadata: JSONObject,
  source: MultiLineString,
});
export type MarkdownCell = D.TypeOf<typeof MarkdownCell>;

const RawCell = D.struct({
  cell_type: D.literal("raw"),
  metadata: JSONObject,
  source: MultiLineString,
});

const Cells = D.array(D.union(CodeCell, MarkdownCell, RawCell));
export type Cells = D.TypeOf<typeof Cells>;

export const RawIpynbMetadata = pipe(
  D.struct({
    minecraft: D.struct({
      description: D.string,
      page_id: D.string,
      page_name: D.string,
      style: D.string,
    }),
  }),
  D.intersect(D.partial({ etag: D.string }))
);

export const IpynbDataDecoder = D.struct({
  cells: Cells,
  metadata: RawIpynbMetadata,
  nbformat: D.literal(4),
  nbformat_minor: D.number,
});
export type IpynbV4 = D.TypeOf<typeof IpynbDataDecoder>;
