import { IEvent } from "../types/IEvent";

export class MockEvent implements IEvent {
  private events: { [name: string]: EventListener[] } = {};
  private eventsOnce: { [name: string]: EventListener[] } = {};

  on(event: string, listener: EventListener): void {
    if (typeof this.events[event] !== "object") {
      this.events[event] = [];
    }
    this.events[event].push(listener);
  }

  removeListener(event: string, listener: EventListener): void {
    let idx: number;
    if (typeof this.events[event] === "object") {
      idx = this.events[event].indexOf(listener);
      if (idx > -1) {
        this.events[event].splice(idx, 1);
      }
    }
  }

  once(event: string, listener: EventListener): void {
    if (typeof this.eventsOnce[event] !== "object") {
      this.eventsOnce[event] = [];
    }
    this.eventsOnce[event].push(listener);
  }
}
