import { v4 as uuidv4 } from "uuid";
import { assertUnreachable } from "../asserts/assertUnreachable";
import { JsonParse } from "../helpers";
import { getUuid } from "../helpers/uuidFromString";
import { LibrariesFilterParameter } from "../store/model";

import {
  ILibraryProviderItemWithSource,
  LibraryProviderItem,
  LibraryProviderItems,
  LibraryProviderItemWithSource,
} from "../types/UserDefinedLibraries";

export const libraryBaseKey = "aznb-local-lib";
export interface ICodeLibraryProvider {
  add: (input: Pick<ILibraryProviderItemWithSource, "name" | "source">) => ILibraryProviderItemWithSource;
  addWithId: (
    input: Required<
      Pick<ILibraryProviderItemWithSource, "name" | "source" | "id" | "predefined" | "readonly" | "order">
    >
  ) => ILibraryProviderItemWithSource;
  update: (
    libraryId: ILibraryProviderItemWithSource["id"],
    key: "source" | "name",
    val: string
  ) => ILibraryProviderItemWithSource | undefined;
  getLibraryItemWithSource: (
    libraryId: ILibraryProviderItemWithSource["id"]
  ) => ILibraryProviderItemWithSource | undefined;
  getLibItems: () => LibraryProviderItems;
  getLibraryItem: (libraryId: ILibraryProviderItemWithSource["id"]) => LibraryProviderItem | undefined;
  delete: (libraryId: ILibraryProviderItemWithSource["id"]) => void;
  getSortedLibraryItems: (filter: LibrariesFilterParameter) => LibraryProviderItem[];
}

export class CodeLibraryProvider implements ICodeLibraryProvider {
  private static instance: CodeLibraryProvider;
  private libraryProviderItems: LibraryProviderItems | undefined = undefined;
  private constructor(private storage: Storage) {}

  public static getInstance(storage: Storage): CodeLibraryProvider {
    if (!CodeLibraryProvider.instance) {
      CodeLibraryProvider.instance = new CodeLibraryProvider(storage);
    }
    return CodeLibraryProvider.instance;
  }
  public getSortedLibraryItems = (filter: LibrariesFilterParameter): LibraryProviderItem[] => {
    const items = CodeLibraryProviderInstance.getLibItems();
    const storedLibraries = () => Object.keys(items).map((key) => items[key]);

    switch (filter) {
      case "all":
        return storedLibraries();
      case "predefined":
        return storedLibraries()
          .filter((i) => i.predefined)
          .sort((i: LibraryProviderItem, j: LibraryProviderItem) => {
            return (i.order || 0) > (j.order || 0) ? 1 : -1;
          });
      case "custom":
        return storedLibraries()
          .filter((i) => i.predefined !== true)
          .sort((i: LibraryProviderItem, j: LibraryProviderItem) => {
            return i.name > j.name ? 1 : -1;
          });
      default:
        return assertUnreachable(filter);
    }
  };
  public add({
    name,
    source,
  }: Pick<ILibraryProviderItemWithSource, "name" | "source">): ILibraryProviderItemWithSource {
    const id = uuidv4();
    const newItem: ILibraryProviderItemWithSource = {
      id,
      name,
      source,
      timestamp: Date.now(),
      predefined: false,
      readonly: false,
      uid: getUuid(libraryBaseKey),
    };
    this.storage.setItem(this.buildKey(id), JSON.stringify(newItem));
    this.libraryProviderItems = undefined;
    return newItem;
  }

  public addWithId({
    name,
    source,
    id,
    readonly,
    predefined,
    order,
  }: Required<
    Pick<ILibraryProviderItemWithSource, "name" | "source" | "id" | "predefined" | "readonly" | "order">
  >): ILibraryProviderItemWithSource {
    const newItem: ILibraryProviderItemWithSource = {
      id,
      name,
      source,
      timestamp: Date.now(),
      predefined,
      readonly,
      order,
      uid: getUuid(libraryBaseKey),
    };

    this.storage.setItem(this.buildKey(id), JSON.stringify(newItem));
    this.libraryProviderItems = undefined;
    return newItem;
  }

  public update(libraryId: string, key: "source" | "name", val: string): ILibraryProviderItemWithSource | undefined {
    const existedLibrary: LibraryProviderItem | undefined = this.getLibItems()[libraryId];
    if (existedLibrary?.readonly || (existedLibrary?.predefined && key === "name")) {
      console.error("can not update library", libraryId);
      return undefined;
    }
    const itemKey = this.buildKey(libraryId);
    const timestamp = Date.now();
    const json = JsonParse(this.storage.getItem(itemKey))(LibraryProviderItemWithSource);
    if (json._tag === "Right") {
      const libItem = json.right;
      libItem[key] = val;
      libItem.timestamp = timestamp;
      this.storage.setItem(itemKey, JSON.stringify(libItem));
      this.libraryProviderItems = undefined;
      return libItem;
    } else {
      console.error(json.left);
      return undefined;
    }
  }

  private buildKey(id: ILibraryProviderItemWithSource["id"]) {
    return `${libraryBaseKey}/${id}`;
  }

  public getLibraryItemWithSource = (libraryId: string): ILibraryProviderItemWithSource | undefined => {
    const itemKey = this.buildKey(libraryId);
    const json = JsonParse(this.storage.getItem(itemKey))(LibraryProviderItemWithSource);
    if (json._tag === "Right") {
      const { timestamp, name, id, source, predefined, readonly } = json.right;
      return { timestamp, name, id, source, predefined, readonly, uid: getUuid(libraryBaseKey) };
    } else {
      return undefined;
    }
  };

  public getLibItems(): LibraryProviderItems {
    if (this.libraryProviderItems) {
      return this.libraryProviderItems;
    } else {
      const length = localStorage.length;
      this.libraryProviderItems = {};

      for (let i = 0; i < length; i++) {
        const key = this.storage.key(i);
        if (key?.startsWith(libraryBaseKey)) {
          const json = JsonParse(this.storage.getItem(key))(LibraryProviderItemWithSource);
          if (json._tag === "Right" && (!json.right.predefined || json.right.uid === getUuid(libraryBaseKey))) {
            const { timestamp, name, id, predefined, readonly, order } = json.right;
            this.libraryProviderItems[id] = { id, name, timestamp, readonly, predefined, order };
          }
        }
      }
      return this.libraryProviderItems;
    }
  }

  public getLibraryItem(libraryId: string): LibraryProviderItem | undefined {
    const library = this.getLibItems()[libraryId];
    return library ? { ...library } : undefined;
  }

  public delete(libraryId: string): void {
    const existedLibrary: LibraryProviderItem | undefined = this.getLibItems()[libraryId];
    if (existedLibrary?.predefined) {
      console.error("can not delete");
      return undefined;
    }
    const itemKey = this.buildKey(libraryId);
    this.storage.removeItem(itemKey);
    this.libraryProviderItems = undefined;
  }
}
export const CodeLibraryProviderInstance = CodeLibraryProvider.getInstance(window.localStorage);
