import create from "zustand/vanilla";
import { AppStoreBuilder, Dependencies, LibrariesFilterParameter, RootState } from "./model";
import { assertUnreachable } from "../asserts/assertUnreachable";

export const appStoreBuilder: AppStoreBuilder = ({ libraryProvider, messageService }: Dependencies) =>
  create<RootState>((set, get) => ({
    href: window.location.href,
    accordionsActiveKeys: [true, true],
    aznbBundleStatus: "idle",
    crossCellState: { kind: "idle", displayedModalWindow: "none" },
    librarySourceEditorOutputPanelData: { isRunning: false, output: null },
    setLibrarySourceEditorOutputPanelData: (librarySourceEditorOutputPanelData) =>
      set(() => {
        return { librarySourceEditorOutputPanelData };
      }),
    setCrossCellState: (crossCellState) =>
      set(() => {
        return {
          crossCellState,
          accordionsActiveKeys: crossCellState.kind === "idle" ? [true, true] : get().accordionsActiveKeys,
        };
      }),

    send: (crossCellMessages) => {
      set(() => {
        messageService.sendMessage(crossCellMessages);
      });
    },

    pushHistoryState: (link) =>
      set(() => {
        window.history.pushState(null, "", link);
        return { href: window.location.href };
      }),
    setAttachedLibraries: (libraryId, checked, crossCellState) =>
      set(() => {
        const diff = { ...crossCellState.diff };
        let attachedLibraryData = crossCellState.attachedLibraryData;

        if (checked) {
          attachedLibraryData = { ...attachedLibraryData, [libraryId]: false };
          diff[libraryId] = (diff[libraryId] || 0) + 1;
        } else {
          const { [libraryId]: value, ...rest } = attachedLibraryData;
          attachedLibraryData = rest;
          diff[libraryId] = (diff[libraryId] || 0) - 1;
        }
        return { crossCellState: { ...crossCellState, diff, attachedLibraryData } };
      }),

    upsertLibraryName: (input, crossCellState) => {
      let { diff, attachedLibraryData, currentLibraryItem } = crossCellState;
      switch (input.action) {
        case "insert": {
          currentLibraryItem = libraryProvider.add({ name: input.libraryName, source: "" });
          diff = { ...diff, [currentLibraryItem.id]: 1 };
          attachedLibraryData = { ...attachedLibraryData, [currentLibraryItem.id]: false };
          get().setAccordionsActiveKey(true, true);
          break;
        }
        case "update": {
          const library = libraryProvider.update(input.libraryId, "name", input.libraryName);
          library && get().send({ action: "LIBRARY_UPDATED", library });
          currentLibraryItem = library ? { ...currentLibraryItem, name: input.libraryName } : currentLibraryItem;
          break;
        }
        default:
          assertUnreachable(input);
          break;
      }
      const { currentBaseCodeCellId } = crossCellState;
      get().setCrossCellState({
        kind: "crud",
        displayedModalWindow: "editLibrarySource",
        attachedLibraryData,
        diff,
        currentBaseCodeCellId,
        currentLibraryItem,
      });
    },
    setCurrentLibrary: (library) =>
      set(() => {
        return { currentLibrary: library };
      }),
    setAccordionsActiveKey: (first: boolean, second: boolean) =>
      set(() => {
        const accordionsActiveKeys: RootState["accordionsActiveKeys"] = [first, second];

        return {
          accordionsActiveKeys,
        };
      }),
    toggleAccordionsActiveKey: (key: 0 | 1) => {
      return set(() => {
        const accordionsActiveKeys: RootState["accordionsActiveKeys"] =
          key === 0
            ? [!get().accordionsActiveKeys[0], get().accordionsActiveKeys[1]]
            : [get().accordionsActiveKeys[0], !get().accordionsActiveKeys[1]];

        return {
          accordionsActiveKeys,
        };
      });
    },
    deleteLibrary: (libraryId, crossCellState) => {
      libraryProvider.delete(libraryId);
      const { [libraryId]: _, ...restAttachedLibraryData } = crossCellState.attachedLibraryData;
      const { [libraryId]: val, ...restDiff } = crossCellState.diff;
      const { currentBaseCodeCellId } = crossCellState;

      get().setCrossCellState({
        kind: "librariesList",
        attachedLibraryData: restAttachedLibraryData,
        diff: restDiff,
        currentBaseCodeCellId,
        displayedModalWindow: "librariesList",
      });
      get().send({
        action: "DELETE_LIBRARY_FROM_STORE",
        activeAznbCellId: currentBaseCodeCellId,
        libraryId: libraryId,
      });
    },
    setAznbBundleStatus: (status) =>
      set(() => {
        return { aznbBundleStatus: status };
      }),
    getFilteredLibraries: (filter: LibrariesFilterParameter) => libraryProvider.getSortedLibraryItems(filter),
  }));
