import {
  RequestHeader,
  CommandRequestBody,
  CommandRequest,
  MinecraftRequest,
  MinecraftResponse,
  RequestBody,
  ResponseBody,
} from "../../types/ipcRenderer";
import { v4 as Guid } from "uuid";

interface IBaseCommand<Result> {
  request: MinecraftRequest;
  responseHandler: (response: MinecraftResponse) => Result | boolean;
}

export interface ICommand<Result> extends IBaseCommand<Result> {
  parse(message: string): Result;
}

export interface IListenerCommand<Result> extends IBaseCommand<Result> {
  parse(body: ResponseBody): Result;
}

// Create the JSON data to subscribe to an event
function createSubscribeRequest(eventName: string, unsubscribe = false): MinecraftRequest {
  const header: RequestHeader = {
    requestId: Guid(),
    messagePurpose: "subscribe",
    version: 1,
    messageType: "commandRequest",
  };

  const body: RequestBody = {
    version: 1,
    eventName: eventName,
  };

  const request: MinecraftRequest = {
    header: header,
    body: body,
  };

  if (unsubscribe) {
    request.header.messagePurpose = "unsubscribe";
  }

  return request;
}

function createCommandRequest(command: string) {
  const header: RequestHeader = {
    requestId: Guid(),
    messagePurpose: "commandRequest",
    version: 1,
    messageType: "commandRequest",
  };

  const body: CommandRequestBody = {
    commandLine: command,
    version: 1,
  };

  const request: CommandRequest = {
    header: header,
    body: body,
  };

  return request;
}

export class GenericCommand<Result> implements ICommand<Result> {
  request: MinecraftRequest;
  throwOnError: boolean;

  constructor(command: string, throwOnError: boolean) {
    this.request = createCommandRequest(command);
    this.throwOnError = throwOnError;
  }

  private handleError(message: string): void {
    if (this.throwOnError) {
      throw new Error(message);
    }
  }

  parse(_statusMessage: string): Result {
    throw new Error("Command must implement parse.");
  }

  responseHandler(response: MinecraftResponse): Result | boolean {
    if (response.body.statusCode) {
      this.handleError(response.body.statusMessage);
    } else if (response.body) {
      return this.parse(response.body.statusMessage);
    } else {
      this.handleError("Incorrect Command Response.");
    }

    return false;
  }
}

export class GenericListenerCommand<Result> implements IListenerCommand<Result> {
  throwOnError: boolean;
  request: MinecraftRequest;

  constructor(eventName: string, throwOnError = false) {
    this.request = createSubscribeRequest(eventName);
    this.throwOnError = throwOnError;
  }

  private handleError(message: string): void {
    if (this.throwOnError) {
      throw new Error(message);
    }
  }

  parse(_body: ResponseBody): Result {
    throw new Error("Command must implement parse.");
  }

  responseHandler(response: MinecraftResponse): Result | boolean {
    if (response.body.statusCode) {
      this.handleError(response.body.statusMessage);
    } else {
      this.handleError("Incorrect Command Response.");
    }

    return false;
  }
}

// Executes any command and ignores the response.
export class AnyCommand extends GenericCommand<void> {
  constructor(command: string, throwOnError = false) {
    super(command, throwOnError);
  }

  parse(_statusMessage: string): undefined {
    return void 0;
  }
}
