import * as D from "io-ts/Decoder";
import { JSONObject } from "./JSONObject";
import { pipe } from "fp-ts/function";
const OnDiskMediaBundle = D.record(D.union(D.string, D.array(D.string), D.UnknownRecord));

const OnDiskExecuteResult = pipe(
  D.struct({
    output_type: D.literal("execute_result"),
    execution_count: D.nullable(D.number),
    data: OnDiskMediaBundle,
    metadata: JSONObject,
  }),
  D.intersect(D.partial({ transient: JSONObject }))
);
export type IPlainOutputExecuteResult = D.TypeOf<typeof OnDiskExecuteResult>;
const OnDiskDisplayData = pipe(
  D.struct({
    output_type: D.literal("display_data"),
    data: OnDiskMediaBundle,
    metadata: JSONObject,
  }),
  D.intersect(D.partial({ transient: JSONObject }))
);
export type IPlainOutputDisplayData = D.TypeOf<typeof OnDiskDisplayData>;
export type IPlainOutput = IPlainOutputExecuteResult | IPlainOutputDisplayData;
const OnDiskStreamOutput = D.struct({
  output_type: D.literal("stream"),
  name: D.union(D.literal("stdout"), D.literal("stderr")),
  text: D.union(D.string, D.array(D.string)),
});

const OnDiskErrorOutput = D.struct({
  output_type: D.literal("error"),
  ename: D.string,
  evalue: D.string,
  traceback: D.array(D.string),
});
export type IErrorOutput = D.TypeOf<typeof OnDiskErrorOutput>;
export const OnDiskOutput = D.array(
  D.union(OnDiskExecuteResult, OnDiskDisplayData, OnDiskStreamOutput, OnDiskErrorOutput)
);
