import React from "react";
import "./index.scss";
export const loadjs = () => {
  return new Promise<void>((resolve, reject) => {
    const jsId = "aznb-js";
    const existingScript = document.getElementById(jsId);
    if (!existingScript) {
      const script = document.createElement("script");
      script.src = "bundle/aznb-bundle.js";
      script.id = jsId;
      document.body.appendChild(script);
      script.onload = () => resolve();
      script.onerror = (evt) => {
        const existingScript = document.getElementById(jsId);
        existingScript ? document.body.removeChild(existingScript) : undefined;
        reject(evt);
      };
    } else {
      resolve();
    }
  });
};
export const loadCss = () => {
  return new Promise<void>((resolve, reject) => {
    const cssId = "aznb-css";
    const existingLink = document.getElementById(cssId);
    if (!existingLink) {
      const head = document.getElementsByTagName("head")[0];
      const link = document.createElement("link");
      link.id = cssId;
      link.rel = "stylesheet";
      link.type = "text/css";
      link.href = "bundle/aznb-bundle.css";
      head.appendChild(link);
      link.onload = () => resolve();
      link.onerror = (evt) => {
        const existingLink = document.getElementById(cssId);
        existingLink ? head.removeChild(existingLink) : undefined;
        reject(evt);
      };
    } else {
      resolve();
    }
  });
};

export class Loader extends React.Component {
  static whyDidYouRender = true;
  render() {
    return (
      <span className="page-loader">
        <img />
      </span>
    );
  }
}
