import * as D from "io-ts/Decoder";
import { pipe } from "fp-ts/function";

export const NonEmptyArrayWithUniqueValues = <T extends string | number>(
  item: D.Decoder<unknown, T>
): D.Decoder<unknown, T[]> =>
  pipe(
    D.array(item),
    D.parse((arr) =>
      arr.length === 0
        ? D.failure(arr, "Array must contain at least one element")
        : arr.length !== new Set(arr).size
        ? D.failure(arr, "Values must be unique")
        : D.success(arr)
    )
  );

export const NonEmptyStringArrayWithUniqueValues = NonEmptyArrayWithUniqueValues(D.string);

export const JsonParse = (str: string | null) => <T>(decoder: D.Decoder<unknown, T>) => {
  try {
    return str ? decoder.decode(JSON.parse(str)) : ({ _tag: "Left", left: "Library was not found" } as const);
  } catch (error) {
    const e = error as SyntaxError;
    return { _tag: "Left", left: e.message } as const;
  }
};
