import { ILogger } from "../types/ILogger";
import { IpynbDataDecoder, IpynbV4 } from "../types/IpynbV4";
import { draw } from "io-ts/Decoder";
import { addLessonLibrariesToAZNB, updateDeleteUserLibraries } from "../helpers/preprocessingLibraries";
import { libraryBaseKey } from "./libraryProvider";
export class NotebookContentError extends Error {
  constructor(message: string, public status?: number) {
    super(message);
    this.name = "NotebookContentError";
  }
}

/**
 *  Handles retrieving Notebook contents from the web or local cache.
 *       Services that manage the storage url should adhere to the following:
 *       - We expect etag to be present in the request header with 'etag' as string. (Make sure header is exposed under CORS)
 *       - We expect Conditional Gets with  `If-None-Match: ${etag}` to work.
 *       - For local file access to work, thr url should handle local file CORS access.
 */
export class NotebookContentProvider {
  private localStorageProvider: Storage;
  private logger: ILogger;
  private baseKey = "meeaznb/";
  // with this flag we will check lesson metadata and in its contain "auto save" value will change save method behavior
  private contentSaveFlag = true;

  constructor(localStorageProvider: Storage, logger: ILogger = console) {
    this.localStorageProvider = localStorageProvider;
    this.logger = logger;
  }

  public async clear(all = false): Promise<void> {
    if (all) {
      this.localStorageProvider.clear();
    } else {
      for (let i = 0; i < this.localStorageProvider.length; i++) {
        const val = this.localStorageProvider.key(i);
        if (val?.startsWith(this.baseKey) || val?.startsWith(libraryBaseKey)) {
          this.localStorageProvider.removeItem(val);
        }
      }
    }
  }

  public buildKey(url: string): string {
    return this.baseKey + url;
  }

  public setContentSaveFlag(val: boolean): void {
    this.contentSaveFlag = val;
  }

  public async save(url: string, notebook: IpynbV4): Promise<void> {
    if (this.contentSaveFlag) {
      this.localStorageProvider.setItem(this.buildKey(url), JSON.stringify(notebook));
    }
  }

  /**
   *
   * @param url - The Notebook url where we will fetch content.
   * @param shouldCache - If this is disabled we don't cache the content.
   */
  public async get(url: string, shouldCache = true): Promise<IpynbV4> {
    let jsonContent = {} as IpynbV4;
    let localEtag: string | undefined;
    {
      const content: string | null = this.localStorageProvider.getItem(this.buildKey(url));
      if (content) {
        try {
          jsonContent = JSON.parse(content);
          localEtag = jsonContent?.metadata?.etag;
        } catch (e) {
          this.logger.log("Invalid json found, disregarding content. Error:" + e.message);
        }
      }
    }

    const httpOptions: RequestInit = {
      method: "GET",
      headers: Object.create(null),
      mode: "cors",
      cache: "no-store", // Don't use platform cache anything, we handle it with localEtags ourselves.
    };
    if (localEtag && !localEtag.startsWith("last-modified")) {
      httpOptions.headers = {
        ...httpOptions.headers,
        "If-None-Match": localEtag,
      };
    }

    try {
      const response: Response = await fetch(url, httpOptions);
      if (response.ok) {
        try {
          const etag = response.headers.get("etag");
          const lastModified = response.headers.get("last-modified");
          const lastModifiedTag = lastModified ? "last-modified at " + response.headers.get("last-modified") : null;
          if (!etag && lastModifiedTag && localEtag && localEtag === lastModifiedTag) {
            this.logger.log(`Using localStorage cache for ${url} based on Last-Modified response header`);
            return updateDeleteUserLibraries(jsonContent);
          }

          let notebook: IpynbV4 = await response.json();
          const decodedObj = IpynbDataDecoder.decode(notebook);
          if (decodedObj._tag === "Left") {
            throw new NotebookContentError(draw(decodedObj.left));
          }
          if (!notebook.metadata) {
            // eslint-disable-next-line @typescript-eslint/ban-ts-comment
            //@ts-ignore
            notebook.metadata = {};
          }
          notebook.metadata.etag = etag || lastModifiedTag || undefined;
          notebook = addLessonLibrariesToAZNB(notebook);

          if (shouldCache) {
            this.save(url, notebook);
          }
          return notebook;
        } catch (e) {
          throw new NotebookContentError(response.statusText, response.status);
        }
      } else {
        if (response.status === 304) {
          this.logger.log(`Using localStorage cache for ${url}`);
          return updateDeleteUserLibraries(jsonContent);
        } else {
          throw new NotebookContentError(response.statusText, response.status);
        }
      }
    } catch (e) {
      throw new NotebookContentError(e.statusText, e.status);
    }
  }
}
