import * as D from "io-ts/Decoder";
import { pipe } from "fp-ts/function";
import { MinecraftMetadata } from "./MinecraftMetadata";

export const LibraryId = D.string;
export const LibraryTimestamp = D.number;
export type LibraryIdType = D.TypeOf<typeof LibraryId>;
export const LibraryProviderItemWithSource = pipe(
  D.struct({
    id: LibraryId,
    name: D.string,
    source: D.string,
    timestamp: LibraryTimestamp,
    uid: D.string,
  }),
  D.intersect(D.partial({ predefined: D.boolean })),
  D.intersect(D.partial({ readonly: D.boolean })),
  D.intersect(D.partial({ order: D.number }))
);

export type ILibraryProviderItemWithSource = D.TypeOf<typeof LibraryProviderItemWithSource>;

export type LibraryProviderItem = Pick<
  ILibraryProviderItemWithSource,
  "id" | "name" | "timestamp" | "predefined" | "readonly" | "order"
>;

export type LibraryProviderItems = Record<string, LibraryProviderItem>;
export type LibraryProviderItemsWithSource = Record<string, ILibraryProviderItemWithSource>;
export type NewLibraryCellParameter = Required<
  Pick<
    MinecraftMetadata,
    "baseKey" | "libraryId" | "expand" | "codeStatus" | "readonly" | "isPredefined" | "isPreAttached"
  >
>;
