import { v5 } from "uuid";
import { ExtractUrlParams } from "../UrlReader";
const namespace = "f5b8ebe5-592a-4640-9e27-8d788100229d";
export const getUuid = (str: string): string => {
  const params = ExtractUrlParams();
  return v5(params.rootNotebookPath + params.lessonJsonPath + "/" + str, namespace);
};
