import { IErrorOutput } from "../types/OnDiskOutput";

export const getLineNumbersFromErrorOutput = (traceback: string[]): number[] => {
  let lineNumbers: number[] = [];
  traceback.forEach((value) => {
    const newLineNumbers = value
      .split("\n")
      .map((lines) => {
        const match = lines.match(/File "CodeBuilder Script", line (\d+)/);
        return match ? Number(match[1]) : -1;
      })
      .filter((lineNumber) => lineNumber > 0);

    lineNumbers = lineNumbers.concat(newLineNumbers);
  });
  return lineNumbers;
};

const getBaseCellOffset = (libraryCellLines: Record<string, { from: number; to: number }>): number =>
  Math.max(...Object.keys(libraryCellLines).map((item) => libraryCellLines[item].to)) || 0;

const getLibraryCellOffset = (
  libraryCellLines: Record<string, { from: number; to: number }>,
  libraryCellId: string,
  lineNumber: number
): number =>
  libraryCellLines[libraryCellId].from === 1 ? lineNumber : lineNumber - libraryCellLines[libraryCellId].from + 1;

const getLibraryCellId = (
  libraryCellLines: Record<string, { from: number; to: number }>,
  lineNumber: number
): string | undefined => {
  return Object.keys(libraryCellLines).find(
    (item) => libraryCellLines[item].from <= lineNumber && libraryCellLines[item].to >= lineNumber
  );
};

export const getErrorData = (
  output: IErrorOutput,
  libraryCellLines: Record<string, { from: number; to: number }>
): {
  librariesErrorLineNumbers: Record<string, number[]>;
  baseCellErrorLineNumbers: number[];
  firstErrorLineNumberAndCellId: { cellId: string; lineNumber: number };
} => {
  const librariesErrorLineNumbers: Record<string, number[]> = {};
  const baseCellErrorLineNumbers: number[] = [];
  let firstErrorLineNumberAndCellId = { cellId: "", lineNumber: Number.MAX_SAFE_INTEGER };
  getLineNumbersFromErrorOutput(output.traceback).forEach((errorLineNumber) => {
    const libraryCellId = getLibraryCellId(libraryCellLines, errorLineNumber);
    if (libraryCellId) {
      const lineNumber = getLibraryCellOffset(libraryCellLines, libraryCellId, errorLineNumber);
      firstErrorLineNumberAndCellId =
        errorLineNumber < firstErrorLineNumberAndCellId.lineNumber
          ? { cellId: libraryCellId, lineNumber: errorLineNumber }
          : firstErrorLineNumberAndCellId;
      librariesErrorLineNumbers[libraryCellId]
        ? librariesErrorLineNumbers[libraryCellId].push(lineNumber)
        : (librariesErrorLineNumbers[libraryCellId] = [lineNumber]);
    } else {
      const offset = getBaseCellOffset(libraryCellLines);
      baseCellErrorLineNumbers.push(errorLineNumber - offset);
    }
  });
  return {
    librariesErrorLineNumbers,
    baseCellErrorLineNumbers,
    firstErrorLineNumberAndCellId,
  };
};
