import { IIpcWorldStorage, CodeStatus } from "../types/ipcWorldStorage";
import { MockEvent } from "./mockEvent";

export class MockIpcWorldStorage extends MockEvent implements IIpcWorldStorage {
  private callbacks?: IIpcWorldStorage;
  // 'codeStatus' is declared but its value is never read.
  protected codeStatus?: CodeStatus;

  constructor(callbacks?: IIpcWorldStorage) {
    super();
    this.callbacks = callbacks;
    this.codeStatus = CodeStatus.notStarted;
    console.log("Creating Mock IpcWorldStorage");
  }

  setCodeStatus(codeStatus: CodeStatus): void {
    if (this.callbacks) {
      this.callbacks.setCodeStatus(codeStatus);
    } else {
      this.codeStatus = codeStatus;
    }
  }
}
