/* eslint-disable indent */
import { assertUnreachable, getPageData, RootState } from "core-library";
import React, { Suspense } from "react";
import shallow from "zustand/shallow";
import { loadCss, Loader, loadjs } from "./components/loader";
import { useStore } from "./store";
import { retry } from "./utils/componentLoader";

const NotebookComponent = React.lazy(() => import(/* webpackChunkName: "az-notebook" */ "./components/notebook"));
const LandingPageComponent = React.lazy(() => import(/* webpackChunkName: "landing-page" */ "./components/landing"));

type Props = Pick<RootState, "setAznbBundleStatus" | "href" | "pushHistoryState" | "aznbBundleStatus">;

export class AppClassComponent extends React.PureComponent<Props, never> {
  async componentDidMount() {
    const { pushHistoryState, setAznbBundleStatus } = this.props;
    window.addEventListener("popstate", () => {
      pushHistoryState(window.location.href);
    });
    try {
      // load aznb js and css files
      const loadJsTask = retry(loadjs, "loadJsTask");
      const loadCssTask = retry(loadCss, "loadCssTask");
      await loadJsTask;
      await loadCssTask;
      setAznbBundleStatus("ok");
    } catch (error) {
      setAznbBundleStatus("error");
    }
  }
  render() {
    const { aznbBundleStatus } = this.props;
    let innerComponent: JSX.Element;
    const parameters = getPageData();
    switch (parameters.page) {
      case "landing":
        innerComponent = (
          <Suspense fallback={<Loader />}>
            <LandingPageComponent />
          </Suspense>
        );
        break;
      case "lesson":
        if (aznbBundleStatus === "error") {
          throw new Error("Failed to download AZNB Bundle FIles.");
        }
        innerComponent =
          aznbBundleStatus !== "ok" ? (
            <Loader />
          ) : (
            <Suspense fallback={<Loader />}>
              <NotebookComponent parameters={parameters} />
            </Suspense>
          );
        break;

      default:
        assertUnreachable(parameters);
    }

    return <React.Fragment>{innerComponent}</React.Fragment>;
  }
}

export const App = () => {
  const { aznbBundleStatus, href } = useStore(
    (state) => ({ href: state.href, aznbBundleStatus: state.aznbBundleStatus }),
    shallow
  );
  const { pushHistoryState, setAznbBundleStatus } = useStore((state) => ({
    pushHistoryState: state.pushHistoryState,
    setAznbBundleStatus: state.setAznbBundleStatus
  }));
  return (
    <AppClassComponent
      aznbBundleStatus={aznbBundleStatus}
      href={href}
      pushHistoryState={pushHistoryState}
      setAznbBundleStatus={setAznbBundleStatus}
    />
  );
};
if (process.env.NODE_ENV === "development") {
  App.whyDidYouRender = true;
}
