import { GenericListenerCommand } from "./Commands";

export enum RuntimeAction {
  Start = "Start",
  Stop = "Stop",
  Pause = "Pause",
}

export interface ICodeBuilderRuntimeAction {
  RuntimeAction: RuntimeAction; // Can be start, stop, pause
}

export class CodebuidlerRuntimeActionCommand extends GenericListenerCommand<ICodeBuilderRuntimeAction> {
  constructor(throwOnError = false) {
    super(`CodeBuilderRuntimeAction`, throwOnError);
  }
  /* eslint-disable @typescript-eslint/no-explicit-any */
  /* eslint-disable @typescript-eslint/explicit-module-boundary-types */
  parse(body: any): ICodeBuilderRuntimeAction {
    /* eslint-enable @typescript-eslint/no-explicit-any */
    /* eslint-enable @typescript-eslint/explicit-module-boundary-types */
    if (body.properties && body.properties.RuntimeAction !== undefined) {
      return { RuntimeAction: body.properties.RuntimeAction } as ICodeBuilderRuntimeAction;
    } else {
      throw new Error(
        `CodebuidlerRuntimeActionCommand::Parse does not contain required values body: ${JSON.stringify(body)}`
      );
    }
  }
}
