import { v4 as uuidv4 } from "uuid";
import { MoveDirection } from "@aznb/core-contract";

import {
  CrossCellMetadataBaseCodeCell,
  CrossCellMetadataLibraryCodeCell,
  MinecraftMetadata,
} from "../types/MinecraftMetadata";
import { MetadataNamespace } from "../types/MinecraftMetadata";
import type { DeepReadonly } from "../types/utility.types";
import { ILibraryProviderItemWithSource, LibraryIdType } from "../types/UserDefinedLibraries";
export type CellId = string;
export interface ICells {
  getMetadata: any;
  getAllCellIds: any;
}
export type LibraryCellPosition =
  | { kind: "custom"; position: "alone" | "first" | "last" | "other" }
  | { kind: "preAttached" };
export type AZNBMetadata = [CellId, DeepReadonly<MinecraftMetadata>][];
export const isCrossCellMetadataBaseCodeCell = (
  item: DeepReadonly<MinecraftMetadata> | undefined
): item is DeepReadonly<CrossCellMetadataBaseCodeCell> => {
  return !!item?.connectedLibraries;
};

export const isCrossCellMetadataLibraryCodeCell = (
  item: DeepReadonly<MinecraftMetadata> | undefined
): item is DeepReadonly<CrossCellMetadataLibraryCodeCell> => {
  return !!item?.libraryId;
};

const getMetadata = (cells: ICells, cellId: CellId): DeepReadonly<MinecraftMetadata> =>
  (cells.getMetadata(cellId, MetadataNamespace) as DeepReadonly<MinecraftMetadata>) || {};

const getAznbMetadata = (cells: ICells): AZNBMetadata => {
  const result: AZNBMetadata = [];
  cells.getAllCellIds().forEach((id: any) => {
    const cellId: string = id;
    const metadata = cells.getMetadata(cellId, MetadataNamespace) as DeepReadonly<MinecraftMetadata> | null;
    if (metadata) {
      result.push([cellId, metadata]);
    }
  });
  return result;
};

const getConnectedLibraryCellId = (
  cells: ICells,
  { libraryId, baseKey }: Pick<MinecraftMetadata, "baseKey" | "libraryId">
): string | undefined =>
  getAznbMetadata(cells).find((cell) => cell[1].libraryId === libraryId && cell[1].baseKey === baseKey)?.[0];

const getBaseCellId = (cells: ICells, { baseKey }: Pick<MinecraftMetadata, "baseKey">): CellId | undefined => {
  return getAznbMetadata(cells).find((cell) => !cell[1].libraryId && cell[1].baseKey === baseKey)?.[0];
};

const getBaseCellIds = (cells: ICells, { libraryId }: Pick<MinecraftMetadata, "libraryId">): CellId[] => {
  const baseKeys = getAznbMetadata(cells)
    .filter((cell) => cell[1].libraryId === libraryId)
    .map((cell) => cell[1].baseKey);

  return getAznbMetadata(cells)
    .filter((cell) => !cell[1].libraryId && cell[1].baseKey && baseKeys.indexOf(cell[1].baseKey) > -1)
    .map((cell) => cell[0]);
};
const getConnectedLibrariesCellIds = (cells: ICells, baseCellId: CellId): CellId[] => {
  const { connectedLibraries = [], baseKey } = getMetadata(cells, baseCellId) ?? {};
  return connectedLibraries
    .map((libraryId) => getConnectedLibraryCellId(cells, { libraryId, baseKey }))
    .filter((f) => f) as CellId[];
};
const getConnectedLibrariesCellIdsSet = (cells: ICells, baseCellId: string): Set<CellId> => {
  return new Set<string>(getConnectedLibrariesCellIds(cells, baseCellId));
};
const getLibraryCellPosition = (cells: ICells, libCellId: CellId): LibraryCellPosition => {
  const metadata = getMetadata(cells, libCellId);
  if (metadata?.isPreAttached) {
    return { kind: "preAttached" };
  }
  const baseKey = metadata?.baseKey;
  const baseCellId = baseKey && getBaseCellId(cells, { baseKey });
  const notPreAttachedConnectedLibrariesCellIds = baseCellId
    ? getConnectedLibrariesCellIds(cells, baseCellId).filter(
        (libraryCellId) => getMetadata(cells, libraryCellId)?.isPreAttached !== true
      )
    : [];

  const length = notPreAttachedConnectedLibrariesCellIds.length;
  if (length === 0) {
    return { kind: "custom", position: "other" };
  }
  if (length === 1) {
    return { kind: "custom", position: "alone" };
  }
  if (length > 1) {
    if (notPreAttachedConnectedLibrariesCellIds[0] === libCellId) {
      return { kind: "custom", position: "first" };
    }
    if (notPreAttachedConnectedLibrariesCellIds[length - 1] === libCellId) {
      return { kind: "custom", position: "last" };
    }
  }
  return { kind: "custom", position: "other" };
};

const changeLibraryOrderData = (
  cells: ICells,
  libCellId: CellId,
  order: MoveDirection
):
  | {
      set: Set<string>;
      connectedLibraries: string[];
      baseCellId: string;
    }
  | undefined => {
  const libraryCellMetadata = getMetadata(cells, libCellId);
  const baseCellId = getBaseCellId(cells, { baseKey: libraryCellMetadata?.baseKey });
  if (baseCellId) {
    const baseCellMetadata = getMetadata(cells, baseCellId);
    const connectedLibraries = [...(baseCellMetadata?.connectedLibraries ?? [])];
    const libraryCurrentIndex = connectedLibraries.findIndex((i) => i === libraryCellMetadata?.libraryId);
    const otherLibraryCurrentIndex = order === "up" ? libraryCurrentIndex - 1 : libraryCurrentIndex + 1;
    if (otherLibraryCurrentIndex > -1) {
      const pointer = connectedLibraries[libraryCurrentIndex];
      connectedLibraries[libraryCurrentIndex] = connectedLibraries[otherLibraryCurrentIndex];
      connectedLibraries[otherLibraryCurrentIndex] = pointer;
      return {
        set: getConnectedLibrariesCellIdsSet(cells, baseCellId),
        connectedLibraries,
        baseCellId,
      };
    }
  }
  return void 0;
};
const getAttachedLibraryData = (cells: ICells, cellId: CellId): Record<string, boolean> => {
  const cell = getMetadata(cells, cellId);
  return isCrossCellMetadataBaseCodeCell(cell)
    ? cell.connectedLibraries.reduce((acc, libraryId) => {
        const libraryCellId = getConnectedLibraryCellId(cells, { libraryId, baseKey: cell.baseKey });
        const isPreAttached = !libraryCellId ? false : !!getMetadata(cells, libraryCellId)?.isPreAttached;
        acc[libraryId] = isPreAttached;
        return acc;
      }, {} as Record<string, boolean>)
    : {};
};

const setBaseCellLibraries = ({
  update,
  baseCellId,
  cells,
  getLibraryItemWithSourceFn,
  deleteCellFn,
  insertCellFn,
  updateLibraryCodeCellMetadataAndSourceFn,
}: {
  update: Record<LibraryIdType, number>;
  baseCellId: CellId;
  cells: ICells;
  getLibraryItemWithSourceFn: (libraryId: string) => ILibraryProviderItemWithSource | undefined;
  deleteCellFn: (cellId: string) => void;
  insertCellFn?: (cellId: string, source: string, metadata: CrossCellMetadataLibraryCodeCell) => void;
  updateLibraryCodeCellMetadataAndSourceFn: (
    cellId: string,
    item: DeepReadonly<MinecraftMetadata>,
    source: string | null
  ) => void;
}): void => {
  const metadata = getMetadata(cells, baseCellId);
  const baseKey = metadata?.baseKey || uuidv4();
  const toDeleteIds: string[] = [];
  const toAddIds: string[] = [];
  Object.keys(update).forEach((key) => {
    update[key] < 0 ? toDeleteIds.push(key) : update[key] > 0 ? toAddIds.push(key) : undefined;
  });
  const connectedLibraries = metadata?.connectedLibraries?.filter((i) => !(update[i] === -1)) || [];
  connectedLibraries.push(...toAddIds);
  toDeleteIds.forEach((libraryId) => {
    const cellIdToDelete = getConnectedLibraryCellId(cells, { libraryId, baseKey });
    cellIdToDelete && deleteCellFn(cellIdToDelete);
  });
  toAddIds.forEach((libraryId) => {
    const library = getLibraryItemWithSourceFn(libraryId);

    const isPredefined = library?.predefined || false;
    const readonly = library?.readonly || false;
    const source = library?.source || "";
    const timestamp = library?.timestamp || 0;
    insertCellFn?.(baseCellId, source, {
      libraryId,
      baseKey,
      isPredefined,
      expand: true,
      errorData: "",
      timestamp,
      codeStatus: "ok",
      isPreAttached: false,
      readonly,
    });
  });
  updateLibraryCodeCellMetadataAndSourceFn(baseCellId, { baseKey, connectedLibraries }, null);
};

export {
  getMetadata,
  getConnectedLibraryCellId,
  getBaseCellId,
  getBaseCellIds,
  getConnectedLibrariesCellIds,
  getConnectedLibrariesCellIdsSet,
  getLibraryCellPosition,
  changeLibraryOrderData,
  getAttachedLibraryData,
  setBaseCellLibraries,
};
