import { GenericCommand, GenericListenerCommand } from "./Commands";

export class GetScoreboardCommand extends GenericCommand<number | boolean> {
  constructor(objectiveName: string, selector: string, throwOnError = false) {
    super(`scoreboard players get ${selector} ${objectiveName}`, throwOnError);
  }

  parse(message: string): number | boolean {
    const num = Number(message);
    return isNaN(num) ? false : num;
  }
}

export class GetAndListenScoreboardCommand extends GenericCommand<number | boolean> {
  constructor(objectiveName: string, selector: string, throwOnError = false) {
    super(`codebuilder subscribe scoreboard ${selector} ${objectiveName}`, throwOnError);
  }

  parse(message: string): number | boolean {
    const num = Number(message);
    return !isNaN(num) ? num : false;
  }
}

export interface ICodeBuilderScoreChanged {
  Objective: string;
  Score: number;
}

export class CodeBuilderScoreChangedCommand extends GenericListenerCommand<ICodeBuilderScoreChanged | boolean> {
  constructor(throwOnError = false) {
    super("CodeBuilderScoreChanged", throwOnError);
  }

  /* eslint-disable @typescript-eslint/no-explicit-any */
  /* eslint-disable @typescript-eslint/explicit-module-boundary-types */
  parse(body: any): ICodeBuilderScoreChanged {
    /* eslint-enable @typescript-eslint/no-explicit-any */
    /* eslint-enable @typescript-eslint/explicit-module-boundary-types */
    if (body.properties?.Objective && body.properties.Score !== undefined) {
      const num = Number(body.properties.Score);
      if (isNaN(num)) {
        throw new Error(`CodeBuilderScoreChangedCommand::Parse does not contain a number.: ${body.properties.Score}`);
      } else {
        return { Objective: body.properties.Objective, Score: num };
      }
    } else {
      throw new Error(
        `CodeBuilderScoreChangedCommand::Parse does not contain required values body:${JSON.stringify(body)}`
      );
    }
  }
}
