import { LOCALES, LocalesType } from "./types/Locales";

export interface IMcParams {
  // The Path of the lesson.json. Can be a relative path or an absolute url
  lessonJsonPath: string;
  // The root direcotry ofr the lesson.json. This is where all lessons are stored
  rootNotebookPath: string;
  // Current Language. This is always passed in by minecraft.
  language?: string;
  // Flag to indicate that we are inGame - Mocks will throw an error
  inGame: boolean;
  // flag for testing issues.
  debug: boolean;
  // Flag to disable lesson data (.json) caching
  disableLessonDataCache: boolean;
}

export function IsInGame(): boolean {
  const searchParams = window?.location.search;
  return !!(searchParams && new URLSearchParams(searchParams).get("inGame") === "1");
}

/**
 * Grab the Url paramters from the browser location object.
 *
 * @param location - Maps to the window.location object
 */
export function ExtractUrlParams(location = window.location): IMcParams {
  const rootUrl: string =
    window.location.origin + window.location.pathname.substring(0, location.pathname.lastIndexOf("/"));
  return ReadUrlParams(window.location.search, rootUrl);
}

/**
 * Parse url parameters from query string
 *
 * @param urlQueryString - query string
 * @param rootUrl - host origin
 */
export function ReadUrlParams(urlQueryString: string, rootUrl = window.location.origin): IMcParams {
  const searchParams = new URLSearchParams(urlQueryString);
  let lessonJsonPath = searchParams.get("lesson");
  const URL = "https://notebooks.minecrafteduservices.com/everglade/content/row/master/tutorials/tutorials/";
  searchParams.get("source") === "remote" ? (lessonJsonPath = URL + lessonJsonPath) : undefined;
  if (!lessonJsonPath) {
    throw new Error("Invalid Lesson Json.");
  }

  let rootNotebookPath = searchParams.get("notebookRoot");
  if (!rootNotebookPath) {
    const rootPath = lessonJsonPath.substring(0, lessonJsonPath.lastIndexOf("/") + 1);
    if (rootPath.toLowerCase().startsWith("http")) {
      rootNotebookPath = rootPath;
    } else {
      // Looks like a relative url. Azure Notebook Loader expects a complete url.
      rootNotebookPath = rootUrl + "/" + rootPath;
    }
  }
  const language: string | undefined = searchParams.get("lang") ?? undefined;
  const debug: boolean = searchParams.get("debugMode")?.toLocaleLowerCase() === "true";
  const inGame: boolean = searchParams.get("inGame") === "1";
  const disableLessonDataCache: boolean = searchParams.get("disableLessonDataCache")?.toLocaleLowerCase() === "true";

  return {
    lessonJsonPath,
    rootNotebookPath,
    language,
    debug,
    inGame,
    disableLessonDataCache,
  };
}

export type LandingPage = { page: "landing" };
export type LessonPage = { page: "lesson" } & IMcParams;
export type PageData = LandingPage | LessonPage;

export function getPageData(): PageData {
  const urlSearchParams = new URLSearchParams(window.location.search);
  const urlSearchParamsLocale = urlSearchParams.get("lang");
  const idx = LOCALES.findIndex((l) => l.toLowerCase() === urlSearchParamsLocale?.toLowerCase());
  if (idx === -1) {
    console.error(`Unsupported locale ${urlSearchParamsLocale}`);
  }
  const defaultLocale: LocalesType = "en-US";
  urlSearchParams.delete("lang");
  urlSearchParams.append("lang", idx === -1 ? defaultLocale : LOCALES[idx]);
  window.history.replaceState(null, "", "?" + urlSearchParams.toString());

  const lesson = urlSearchParams.get("lesson");
  if (lesson) {
    return { page: "lesson", ...ExtractUrlParams() };
  } else {
    return { page: "landing" };
  }
}
