import { getIpcTelemetry } from "../telemetry";

export function retry(fn: () => Promise<void>, info = "", retriesLeft = 3, interval = 300) {
  return new Promise<void>((resolve, reject) => {
    fn()
      .then(() => {
        // attempt is set to ${4 - retriesLeft} based on retriesLeft = 3 (default value)
        getIpcTelemetry().log(`${info} function call successfully resolved at attempt ${4 - retriesLeft} `);
        return resolve();
      })
      .catch((error) => {
        setTimeout(() => {
          if (retriesLeft === 1) {
            // reject('maximum retries exceeded');
            reject(error);
            return;
          }

          // Passing on "reject" is the important part
          retry(fn, info, retriesLeft - 1, interval).then(resolve, reject);
        }, interval);
      });
  });
}
