import { AznbbTranslations, LocalesType, Translations } from "core-library";
import { WithT } from "i18next";
import { WithTranslation } from "react-i18next";

export const translate = (t: WithTranslation["t"]) => <K extends keyof Translations>(key: K) =>
  t(key) as Translations[K];

export function getAznbTranslations(fn: WithT["t"]) {
  const t = translate(fn);
  const aznbTranslations: AznbbTranslations = {
    AznbbErrorPrefix: t("AznbbErrorPrefix"),
    AznbbErrorRun: t("AznbbErrorRun"),
    AznbbErrorUnexpected: t("AznbbErrorUnexpected"),
    AznbbReadOnly: t("AznbbReadOnly"),
    AznbbResetCode: t("AznbbResetCode"),
    AznbbResetWorld: t("AznbbResetWorld"),
    AznbbRun: t("AznbbRun"),
    AznbbRunCancelled: t("AznbbRunCancelled"),
    AznbbRunInProcess: t("AznbbRunInProcess"),
    AznbbRunSuccessful: t("AznbbRunSuccessful"),
    AznbbStop: t("AznbbStop"),
    AznbbLibraries: t("AznbbLibraries")
  };
  const locale: LocalesType = t("Locale");
  return { aznbTranslations, locale };
}
