import { Observable, Subject } from "rxjs";
import { CellId } from "../helpers/aznbMetadata";
import { IErrorOutput, IPlainOutput } from "../types/OnDiskOutput";

import type { ILibraryProviderItemWithSource, LibraryProviderItem } from "../types/UserDefinedLibraries";
import type { DeepReadonly } from "../types/utility.types";

export const CrossCellMessages = {
  NONE: "NONE",
  LIBRARY_UPDATED: "LIBRARY_UPDATED",
  LIBRARY_UPDATE: "LIBRARY_UPDATE",
  CREATE_LIBRARY_IN_STORE: "CREATE_LIBRARY_IN_STORE",
  ADD_REMOVE_CONNECTED_LIBRARIES_TO_BASE_CELL: "ADD_REMOVE_CONNECTED_LIBRARIES_TO_BASE_CELL",
  DELETE_LIBRARY_FROM_STORE: "DELETE_LIBRARY_FROM_STORE",
  OPEN_LIBRARIES_LIST_WINDOW: "OPEN_LIBRARIES_LIST_WINDOW",
  HIDE_LIBRARY_EDIT_WINDOW: "HIDE_LIBRARY_EDIT_WINDOW",
  OPEN_LIBRARY_RENAME_WINDOW: "OPEN_LIBRARY_RENAME_WINDOW",
  GET_CURRENT_LIBRARY: "GET_CURRENT_LIBRARY",
  SEND_CURRENT_LIBRARY: "SEND_CURRENT_LIBRARY",
  LOAD_PREDEFINED_LIBRARIES: "LOAD_PREDEFINED_LIBRARIES",
  UPDATE_ARROW_ICONS: "UPDATE_ARROW_ICONS",
  UPDATE_OUTPUT_PANEL_DATA: "UPDATE_OUTPUT_PANEL_DATA",
  TEST_LIBRARY_CODE: "TEST_LIBRARY_CODE",
} as const;

// The service is used to provide communication between the "code cells" of Azure Notebook and the rest react components of the application.
//  as an example
//               if library is renamed with "LIBRARY_UPDATED" action will notify subscribed "library code cells"
//               if "up/down" arrow icon is clicked subscribed "library code cells" will update arrow icons statuses,
//               on delete library from store  "DELETE_LIBRARY_FROM_STORE" will remove all appropriate attached "library code cells"

export type Message =
  | { action: typeof CrossCellMessages.NONE }
  | { action: typeof CrossCellMessages.HIDE_LIBRARY_EDIT_WINDOW }
  | { action: typeof CrossCellMessages.OPEN_LIBRARY_RENAME_WINDOW }
  | { action: typeof CrossCellMessages.TEST_LIBRARY_CODE }
  | {
      action: typeof CrossCellMessages.UPDATE_OUTPUT_PANEL_DATA;
      data: {
        isRunning: boolean;
        output: IPlainOutput | IErrorOutput | null;
      };
    }
  | { action: typeof CrossCellMessages.LIBRARY_UPDATE }
  | {
      action: typeof CrossCellMessages.LIBRARY_UPDATED;
      library: DeepReadonly<ILibraryProviderItemWithSource>;
      cellId?: CellId;
    }
  | { action: typeof CrossCellMessages.CREATE_LIBRARY_IN_STORE; library: DeepReadonly<ILibraryProviderItemWithSource> }
  | {
      action: typeof CrossCellMessages.ADD_REMOVE_CONNECTED_LIBRARIES_TO_BASE_CELL;
      activeAznbCellId: CellId;
      update: Record<string, number>;
    }
  | {
      action: typeof CrossCellMessages.DELETE_LIBRARY_FROM_STORE;
      activeAznbCellId: CellId;
      libraryId: LibraryProviderItem["id"];
    }
  | {
      action: typeof CrossCellMessages.OPEN_LIBRARIES_LIST_WINDOW;
      activeAznbCellId: CellId;
      attachedLibraryData: Record<string, boolean>;
    }
  | { action: typeof CrossCellMessages.GET_CURRENT_LIBRARY }
  | {
      action: typeof CrossCellMessages.SEND_CURRENT_LIBRARY;
      activeAznbCellId: CellId;
      library: DeepReadonly<LibraryProviderItem>;
    }
  | {
      action: typeof CrossCellMessages.UPDATE_ARROW_ICONS;
      connectedLibrariesCellIds: Set<CellId>;
    };
const subject = new Subject<Message>();

export const CrossCellMessageService = {
  sendMessage: (msg: Message): void => {
    subject.next(msg);
  },

  getMessage: (): Observable<Message> => subject.asObservable(),
};
export type ICrossCellMessageService = typeof CrossCellMessageService;
