import React from "react";
import { getIpcTelemetry } from "../telemetry";
import { WithTranslation, withTranslation } from "react-i18next";
import { translate } from "../utils/i18next.utils";

interface IErrorBoundaryState {
  hasError: boolean;
  errorMessage: string;
}

class ErrorBoundaryComponent extends React.Component<WithTranslation, IErrorBoundaryState> {
  state = { hasError: false, errorMessage: "" };

  static getDerivedStateFromError(error: Error) {
    return { hasError: true, errorMessage: error.message };
  }

  componentDidCatch(error: Error) {
    getIpcTelemetry()?.error(error);
  }

  render() {
    if (this.state.hasError) {
      const t = translate(this.props.t);
      return (
        <div className="alert alert-danger effect-alert" role="alert">
          {t("AznbbErrorUnexpected")}
          <br />
          {t("AznbbErrorPrefix") + this.state.errorMessage}
        </div>
      );
    }

    return this.props.children;
  }
}
export const ErrorBoundary = withTranslation()(ErrorBoundaryComponent);
