import { IActiveTab } from "../types/IActiveTab";
import { ILogger } from "../types/ILogger";
import { IMinecraftCommander } from "../communcation/minecraftCommander";
import {
  CodeBuilderScoreChangedCommand,
  GetAndListenScoreboardCommand,
  ICodeBuilderScoreChanged,
} from "../communcation/Commands/Scoreboard";

export interface IActiveTabProvider {
  get(lessonId: string, objective?: string, callback?: (result: IActiveTab) => void): Promise<IActiveTab | undefined>;
  set(lessonId: string, activeTab: IActiveTab): void;
}

export class ActiveTabProvider implements IActiveTabProvider {
  private localStorageProvider: Storage;
  protected logger: ILogger;
  minecraftCommander: IMinecraftCommander;
  private baseKey = "meeaznb/";

  constructor(localStorageProvider: Storage, minecraftCommandProvider: IMinecraftCommander, logger: ILogger = console) {
    this.localStorageProvider = localStorageProvider;
    this.minecraftCommander = minecraftCommandProvider;
    this.logger = logger;
  }

  public buildKey(id: string): string {
    return this.baseKey + id;
  }

  async get(
    lessonId: string,
    objective?: string,
    callback?: (result: IActiveTab) => void
  ): Promise<IActiveTab | undefined> {
    if (objective && callback) {
      this.minecraftCommander.subscribe(
        new CodeBuilderScoreChangedCommand(true),
        (result: ICodeBuilderScoreChanged) => {
          callback({ activeTabIndex: result.Score } as IActiveTab);
        }
      );
      const scoreboardValue = await this.minecraftCommander.send(
        new GetAndListenScoreboardCommand(objective, "@s", false)
      );
      if (scoreboardValue && scoreboardValue > 0) {
        return { activeTabIndex: scoreboardValue } as IActiveTab;
      }
    }

    const content: string | null = this.localStorageProvider.getItem(this.buildKey(lessonId));
    if (content) {
      const num = Number(content);
      if (!isNaN(num) && num > 0) {
        return { activeTabIndex: num } as IActiveTab;
      }
    }
    return undefined;
  }

  set(lessonId: string, activeTab: IActiveTab): void {
    this.localStorageProvider.setItem(this.buildKey(lessonId), activeTab.activeTabIndex.toString());
  }
}
