import { CodeCellMinecraftMetadata } from "./IpynbV4";

type LibraryUI = Required<Pick<CodeCellMinecraftMetadata, "expand" | "codeStatus">>;
type CrossCellSharedMetadata = Required<Pick<CodeCellMinecraftMetadata, "baseKey" | "errorData">>;
export type AttachedLibraries = Record<string, Partial<LibraryUI>>;

export type CrossCellMetadataBaseCodeCell = Pick<
  CodeCellMinecraftMetadata,
  "baseCodeCellName" | "enableCodeLibraryButton"
> &
  Required<Pick<CodeCellMinecraftMetadata, "connectedLibraries">> &
  CrossCellSharedMetadata &
  LibraryUI;

export type CrossCellMetadataLibraryCodeCell = Required<
  Pick<CodeCellMinecraftMetadata, "libraryId" | "timestamp" | "isPredefined" | "isPreAttached" | "readonly">
> &
  LibraryUI &
  CrossCellSharedMetadata;

export type CrossCellMetadata = Partial<CrossCellMetadataBaseCodeCell & CrossCellMetadataLibraryCodeCell>;

export type MinecraftMetadata = CodeCellMinecraftMetadata & CrossCellMetadata;
/**
 * Namespace for minecraft's own metadata of a cell data entity
 */
export const MetadataNamespace = "minecraft";
