import { IIpcTelemetry } from "../types/ipcTelemetry";
import { MockEvent } from "./mockEvent";
import { IsInGame } from "../UrlReader";
import { serialize } from "./logutil";

export class MockIpcTelemetry extends MockEvent implements IIpcTelemetry {
  private callbacks?: IIpcTelemetry;
  protected editorName?: string;
  protected url?: string;
  protected context?: string;

  constructor(callbacks?: IIpcTelemetry) {
    super();
    this.callbacks = callbacks;
    if (IsInGame()) {
      throw new Error("Currently in game. Mock shouldnt be enalbed.");
    } else {
      console.log("Creating Mock IpcTelemetry");
    }
  }

  register(editorName: string, url: string, context?: string): void {
    if (this.callbacks) {
      this.callbacks.register(editorName, url, context);
    } else {
      this.setUrl(url);
      this.setName(editorName);
      this.setContext(context);
    }
  }

  setUrl(url: string): void {
    if (this.callbacks) {
      this.callbacks.setUrl(url);
    } else {
      this.url = url;
    }
  }

  setContext(context?: string): void {
    if (this.callbacks && context) {
      this.callbacks.setContext(context);
    } else {
      this.context = context;
    }
  }

  setName(name: string): void {
    if (this.callbacks) {
      this.callbacks.setName(name);
    } else {
      this.editorName = name;
    }
  }

  log(message: string | unknown): void {
    if (this.callbacks) {
      this.callbacks.log(message);
    } else {
      console.log(`MockIpcTelemetry: ${serialize(message)}`);
    }
  }

  warn(message: string | unknown): void {
    if (this.callbacks) {
      this.callbacks.warn(message);
    } else {
      console.warn(`MockIpcTelemetry: ${serialize(message)}`);
    }
  }

  error(message: string | unknown): void {
    if (this.callbacks) {
      this.callbacks.error(message);
    } else {
      console.error(`MockIpcTelemetry: ${serialize(message)}`);
    }
  }
}
