import { IMinecraftCommander } from "../communcation/minecraftCommander";
import { MockEvent } from "./mockEvent";
import { ICommand, IListenerCommand } from "../communcation/Commands/Commands";
import { IsInGame } from "../UrlReader";
import { serialize } from "./logutil";

export interface MockCommanderCallback {
  onSend<Result>(command: ICommand<Result>): Promise<Result>;
}

export class MockMinecraftCommander extends MockEvent implements IMinecraftCommander {
  private callback?: MockCommanderCallback;

  constructor(callback?: MockCommanderCallback) {
    super();
    this.callback = callback;
    if (IsInGame()) {
      throw new Error("Currently in game. MockCommander shouldn't be enabled.");
    } else {
      console.log("Creating Mock Minecraft Commander");
    }
  }

  subscribe<Result>(_command: IListenerCommand<Result>, _callback: (result: Result) => void): void {
    console.log("Subscription");
  }

  send<Result>(command: ICommand<Result>): Promise<Result> {
    if (this.callback) {
      return this.callback.onSend(command);
    } else {
      console.log(`MockMinecraftCommander.send:${serialize(command.request)}`);
      return Promise.resolve(command.parse("---"));
    }
  }
}
