import * as D from "io-ts/Decoder";
import { pipe } from "fp-ts/function";
import { NonEmptyStringArrayWithUniqueValues } from "../helpers";

const AdditionalLanguages = D.array(D.string);
export type AdditionalLanguages = D.TypeOf<typeof AdditionalLanguages>;

// Scoreboard value to decide which activity to show.
// Called onShow.
const ScoreboardActivityTracker = D.struct({
  activitySelectorObjective: D.string,
});

export type IScoreboardActivityTracker = D.TypeOf<typeof ScoreboardActivityTracker>;

export type IsLanguageSupported = (language: string, additionalLanguages: AdditionalLanguages) => boolean;

const ActivityTracker = D.struct({
  $type: D.literal("scoreboard"),
  data: ScoreboardActivityTracker,
});

export type IActivityTracker = D.TypeOf<typeof ActivityTracker>;

const Library = D.struct({
  name: D.string,
  readonly: D.boolean,
  code: D.string,
});
const Libraries = D.array(Library);
export type Libraries = D.TypeOf<typeof Libraries>;

const LessonDataRequired = D.struct({
  creatorName: D.string, // Used to populate id name
  title: D.string,
  description: D.string,
  pages: NonEmptyStringArrayWithUniqueValues,
});

export const LessonData = pipe(
  LessonDataRequired,
  D.intersect(D.partial({ additionalLanguages: AdditionalLanguages })),
  D.intersect(D.partial({ activityTracker: ActivityTracker })),
  D.intersect(
    D.partial({
      disableUserSaves: D.boolean, // Used to define auto save
    })
  ),
  D.intersect(
    D.partial({
      libraries: Libraries,
    })
  )
);

export type ILessonData = D.TypeOf<typeof LessonData> & { isLanguageSupported?: IsLanguageSupported };
