import i18n from "i18next";
import detector from "i18next-browser-languagedetector";
import { initReactI18next } from "react-i18next";

import translationUS from "./locales/en-us/translation";
import translationBG from "./locales/bg-bg/translation";
import translationCZ from "./locales/cs-cz/translation";
import translationDK from "./locales/da-dk/translation";
import translationDE from "./locales/de-de/translation";
import translationGR from "./locales/el-gr/translation";
import translationGB from "./locales/en-gb/translation";
import translationES from "./locales/es-es/translation";
import translationMX from "./locales/es-mx/translation";
import translationFI from "./locales/fi-fi/translation";
import translationCA from "./locales/fr-ca/translation";
import translationFR from "./locales/fr-fr/translation";
import translationHU from "./locales/hu-hu/translation";
import translationID from "./locales/id-id/translation";
import translationIT from "./locales/it-it/translation";
import translationJP from "./locales/ja-jp/translation";
import translationKR from "./locales/ko-kr/translation";
import translationNO from "./locales/nb-no/translation";
import translationNL from "./locales/nl-nl/translation";
import translationPL from "./locales/pl-pl/translation";
import translationBR from "./locales/pt-br/translation";
import translationPT from "./locales/pt-pt/translation";
import translationRU from "./locales/ru-ru/translation";
import translationSK from "./locales/sk-sk/translation";
import translationSE from "./locales/sv-se/translation";
import translationTR from "./locales/tr-tr/translation";
import translationUA from "./locales/uk-ua/translation";
import translationCN from "./locales/zh-cn/translation";
import translationTW from "./locales/zh-tw/translation";
import translationPS from "./locales/ps-ps/translation";
import { Translations, LocalesType } from "core-library";
/* eslint-disable @typescript-eslint/camelcase */

export const setI18nResources = (): {
  [P in LocalesType]: { translations: Partial<Translations> };
} => {
  return {
    "en-US": {
      translations: translationUS
    },
    "en-GB": {
      translations: translationGB
    },
    "bg-BG": {
      translations: translationBG
    },
    "cs-CZ": {
      translations: translationCZ
    },
    "da-DK": {
      translations: translationDK
    },
    "de-DE": {
      translations: translationDE
    },
    "el-GR": {
      translations: translationGR
    },
    "ru-RU": {
      translations: translationRU
    },
    "es-ES": {
      translations: translationES
    },
    "es-MX": {
      translations: translationMX
    },
    "fi-FI": {
      translations: translationFI
    },
    "fr-CA": {
      translations: translationCA
    },
    "fr-FR": {
      translations: translationFR
    },
    "hu-HU": {
      translations: translationHU
    },
    "id-ID": {
      translations: translationID
    },
    "it-IT": {
      translations: translationIT
    },
    "ja-JP": {
      translations: translationJP
    },
    "ko-KR": {
      translations: translationKR
    },
    "nb-NO": {
      translations: translationNO
    },
    "nl-NL": {
      translations: translationNL
    },
    "pl-PL": {
      translations: translationPL
    },
    "pt-BR": {
      translations: translationBR
    },
    "pt-PT": {
      translations: translationPT
    },
    "sk-SK": {
      translations: translationSK
    },
    "sv-SE": {
      translations: translationSE
    },
    "tr-TR": {
      translations: translationTR
    },
    "uk-UA": {
      translations: translationUA
    },
    "zh-CN": {
      translations: translationCN
    },
    "zh-TW": {
      translations: translationTW
    },
    "ps-PS": {
      translations: translationPS
    }
  };
};

const lng: LocalesType = "en-US";
i18n
  .use(detector)
  .use(initReactI18next)
  .init({
    fallbackLng: lng,
    debug: true,
    detection: { lookupQuerystring: "lang", order: ["querystring"] },
    resources: setI18nResources(),
    defaultNS: "translations"
  });
export { i18n };
