import { CodeCell, IpynbV4 } from "../types/IpynbV4";
import { CrossCellMetadataBaseCodeCell } from "../types/MinecraftMetadata";
import { v4 as uuidv4 } from "uuid";
import { getUuid } from "./uuidFromString";
import { CodeLibraryProviderInstance } from "../storage/libraryProvider";
import { ILessonData } from "../types/ILessonData";

export const addLessonLibrariesToAZNB = (data: IpynbV4, uuid: () => string = uuidv4): IpynbV4 => {
  const baseKey = uuid();
  const cb = (cells: IpynbV4["cells"], item: IpynbV4["cells"][number]) => {
    if (item.cell_type === "code") {
      if (!item.metadata?.minecraft) {
        item.metadata = { minecraft: { expand: true } };
      } else {
        if (item.metadata.minecraft.expand === undefined && !item.metadata.minecraft.hidden) {
          item.metadata = { ...item.metadata, minecraft: { ...item.metadata.minecraft, expand: true } };
        }
      }

      if (item.metadata.minecraft?.attachedLibraries?.length) {
        const baseCodeCell = item.metadata.minecraft as CrossCellMetadataBaseCodeCell;
        baseCodeCell.baseKey = baseKey;
        baseCodeCell.connectedLibraries = [];
        const attachedLibraries = item.metadata.minecraft.attachedLibraries;
        attachedLibraries.forEach((libraryData) => {
          const [libName, libUI] = Object.entries(libraryData)[0];
          const libraryId = getUuid(libName);
          const library = CodeLibraryProviderInstance.getLibraryItemWithSource(libraryId);
          if (library) {
            const predefinedLibraryCell: CodeCell = {
              cell_type: "code",
              outputs: [],
              execution_count: null,
              source: library.source,
              metadata: {
                minecraft: {
                  baseKey,
                  codeStatus: libUI.codeStatus || "ok",
                  errorData: "",
                  expand: libUI.expand !== undefined ? libUI.expand : true,
                  isPreAttached: true,
                  isPredefined: true,
                  libraryId,
                  timestamp: library.timestamp,
                  readonly: !!library.readonly,
                },
              },
            };
            cells.push(predefinedLibraryCell);
            baseCodeCell.connectedLibraries.push(libraryId);
          }
        });
        item.metadata.minecraft.attachedLibraries = [];
      }
    }
    cells.push(item);
    return cells;
  };
  const cells = data.cells.reduce(cb, []);
  data.cells = cells;
  return data;
};

export const addLessonLibrariesToLocalStore = (libraries: ILessonData["libraries"]): void => {
  const libraryItems = CodeLibraryProviderInstance.getLibItems();
  libraries?.forEach((i, idx) => {
    const libraryKey = getUuid(i.name);
    if (!libraryItems[libraryKey]) {
      CodeLibraryProviderInstance.addWithId({
        name: i.name,
        id: libraryKey,
        source: i.code,
        predefined: true,
        readonly: i.readonly,
        order: idx,
      });
    }
  });
};

export const updateDeleteUserLibraries = (data: IpynbV4): IpynbV4 => {
  const cb = (cells: IpynbV4["cells"], item: IpynbV4["cells"][number]) => {
    switch (item.cell_type) {
      case "markdown":
      case "raw":
        cells.push(item);
        break;
      case "code":
        if (item.metadata.minecraft?.libraryId) {
          const library = CodeLibraryProviderInstance.getLibraryItemWithSource(item.metadata.minecraft?.libraryId);
          if (item.metadata.minecraft.libraryId === library?.id) {
            item.metadata.minecraft.timestamp = library.timestamp;
            item.source = library.source;
            cells.push(item);
          }
        } else {
          const metadata = item.metadata.minecraft;
          if (metadata?.connectedLibraries?.length) {
            const libraryItems = CodeLibraryProviderInstance.getLibItems();
            metadata.connectedLibraries = metadata.connectedLibraries.filter((i) => libraryItems[i]);
          }
          cells.push(item);
        }
        break;
      default:
        break;
    }

    return cells;
  };
  const cells = data.cells.reduce(cb, []);
  data.cells = cells;
  return data;
};
