import * as D from "io-ts/Decoder";
const PrimitiveImmutableDecoder = D.union(D.nullable(D.string), D.nullable(D.number), D.nullable(D.boolean));
type PrimitiveImmutable = D.TypeOf<typeof PrimitiveImmutableDecoder>;
type JSONType = PrimitiveImmutable | JSONObject | JSONArray;
export interface JSONObject {
  [key: string]: JSONType;
}
type JSONArray = Array<JSONType>;

export const JSONType: D.Decoder<unknown, JSONType> = D.lazy("JSONType", () =>
  D.union(PrimitiveImmutableDecoder, JSONObject, JSONArray)
);

export const JSONArray: D.Decoder<unknown, JSONArray> = D.lazy("JSONArray", () => D.array(JSONType));
export const JSONObject: D.Decoder<unknown, JSONObject> = D.lazy("JSONObject", () => D.record(JSONType));
